*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    background-color: #ece9e4;
    box-sizing: border-box;
    overflow-x: hidden;
}
header{
    padding: 40px 0px;
}
header{
    box-sizing: border-box;
}
.herosection{
    background: linear-gradient(rgb(236, 233, 228, 0.3), rgb(224, 222, 217, 0.3)), url('../images/Ellipse_1.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    height: 100vh !important;
    position: relative;
    margin-bottom: 150px;

}
.herosection h1{
    color: #194e3c;
    font-size: 90px;
    font-family: 'Prata',Arial,sans-serif;
    line-height: NaN;
    font-weight: 400;
    text-transform: uppercase;
    width: 70%;
}
.brochur_text{
    width: 350px;
    color: #3c3c3c;
    font-size: 18px;
    font-family: 'Inter',Arial,sans-serif;
    line-height: 1.50;
    font-weight: 400;
    position: absolute;
    bottom: 120px;
    right: 0;
}
.brochure{
    background-color: #194e3c;
    border-radius: 50px;
    color: white;
    position: absolute;
    bottom: 0;
    right: 0;
}
.brochure img{
    border-radius: 50%;
}
.video{
    width: 150px !important;
    height: 150px !important;
    border-radius: 50%;
    border: 2px solid #194e3c;
    position: absolute;
    top: 35%;
    right: 17%;
}
.video img{
    width: 30px;
}
.card_title{
    font-size: 20px;

}
.card_sub{
    font-size: 13px;
}


/* section 2 */

.section_text{
    color: #f3f0eb;
    font-size: 45px;
    font-family: 'Prata',Arial,sans-serif;
    font-weight: 400;
    text-transform: uppercase;
    background-position: center center;
    background-color: #194e3c;
    padding: 100px;
    text-align: center;
}
.section_text img{
    width: 100px;
}

/* section3 */

.sectionThree, .sectionFour{
    padding: 80px 0px;
}
.sectionThree .brochure{
    position: relative;
    width: 80%;
}
.sectionThree .title, .sectionFour .title, .sectionFive .title{
    color: #194e3c;
    font-size: 45px;
    font-family: 'Prata',Arial,sans-serif;
    line-height: 1.35;
    font-weight: 400;
    text-transform: uppercase;
    line-height: 61px;
    
}
.keyTitle{
    color: #3c3c3c;
    font-size: 16px;
    font-family: 'Inter',Arial,sans-serif;
    font-weight: 400;
}

/* section four */
.sectionFour .circle, .sectionFour .circleLeft, .sectionSeven .circle, .sectionTen .circle{
    width: 300px;
    height: 300px;
    border: 1px solid #3c3c3c ;
    border-radius: 50%;
    margin-top: 120px;
}
.sectionSeven .circle{
    border: 1px solid #edeae5 ;
    margin-top: 0px;
    width: 320px;
    height: 320px;

}
.circle2{
    position: relative;
}
.sectionFour .circleLeft{
    margin-top: 0px;
    position: absolute;
    top: -50px;
    right: 10%;

}
.sectionFour .circleText, .circleTextLeft, .sectionTen .circleText{
    color: #3c3c3c;
    font-size: 45px;
    font-family: 'Prata',Arial,sans-serif;
    line-height: 1.35;
    font-weight: 400;
    opacity: 0.5;
    text-align: center;
    position: relative;
}
.circleText img{
    position: absolute;
    bottom: -50px;
    right: -10px;
}
.circleTextLeft img{
    position: absolute;
    top: -60px;
    left: -50px;
}
.circleTextLeft{
    font-size: 30px;
}
.recImg{
    width: 100%;
}

/* section five */
.sectionFive{
    padding: 80px 0px;
}
.card_container{
    padding-top: 100px;
}
.card_container .col-lg-3{
    border-right: 1px solid #9c9696 !important;

}
.card{
    padding: 0px 30px 50px;
    background-color: #edeae5;
    border: none ;
    border-radius: 0 !important;
}
.col-lg-3.last{
    border: none !important;
}
.card.even .count{
    margin-top: 50px !important;
}
.card .count{
    color: #3c3c3c;
    font-size: 55px;
    font-family: 'Prata',Arial,sans-serif;
    line-height: 1.35;
    font-weight: 400;
    opacity: 0.5;
    margin-top: -75px;

}
.card .discription, .card .offer{
    color: #3c3c3c;
    font-size: 22px;
    font-family: 'Prata',Arial,sans-serif;
    line-height: 1.35;
    font-weight: 400;
    padding: 30px 0px;
}
.card .offer{
    font-size: 14px;
    font-family: 'Inter',Arial,sans-serif;
    padding: 0px;
}

/* section six */

.sectionSix .brochure{
    width: 100%;
}

/* section seven */
.sectionSeven{
    padding-bottom: 0px;
}

.sectionSeven .col-lg-6{
    position: relative;
}
.sectionSeven h2{
    color: #f3f0eb;
    font-size: 70px !important;
    font-family: 'Prata',Arial,sans-serif;
}
.project{
    width: 50%;
    position: absolute;
    top: 30%;
    right: -20px;
    font-size: 22px;
}
.sectionSeven .left img{
    width: 60%;
    position: absolute;
    top: 50%;
    right: 0;
}
.sectionSeven .initial{
    width: 80%;
    color: #f3f0eb;
    font-size: 22px;
}
.sectionSeven .circle.right{
    width: 500px;
    height: 500px;
    position: relative;
    top: -150px;
}
.sectionSeven .circle.right img{
    width: 100%;
    position: absolute;
    top: 70%;
    left: -70%;
}

/* sectionEight */
.brochure_img{
    transform: rotate(356.7deg);
}
.sectionEight h2{
    color: #194e3c;
    font-size: 45px;
    font-family: 'Prata',Arial,sans-serif;
}
.sectionEight form, .sectionEleven form{
    width: 90%;
    margin-top: 50px;
}
.sectionEight form input, .sectionEight .download_btn, .sectionEleven form input, .sectionEleven .download_btn{
    width: 100%;
    border: none;
    border-bottom: 1px solid #194e3c;
    background: transparent;
    margin-bottom: 30px;
    padding: 15px 15px;

}
.sectionEight .download_btn, .sectionEleven .download_btn{
    padding: 0px 15px;
    display: block;
    width: 430px;
    font-weight: 400;
    height: 60px;
    margin-left: auto;
    margin-right: auto;
    background-color: #194e3c;
    color: white;
    border-radius: 50px;
}
.sectionEleven form{
    background: white !important;
    padding: 30px;
    width: 500px;
    position: relative;
    top: -120px;
    left: 55px;
}
.sectionEleven form h2{
    color: #194e3c;
    font-size: 50px;
    font-family: 'Prata',Arial,sans-serif;
}

/* sectionNine */
.sectionNine{
    padding: 80px 0px;
    box-sizing: border-box;
    overflow-x:  hidden !important;
}
.sectionNine h2{
    
    color: #494949;
    font-size: 80px;
    font-family: 'Prata',Arial,sans-serif;
    line-height: 1.10;
    font-weight: 500;
    text-transform: uppercase;

}
.slider_container{
    margin-top: 50px;
}
.slider_container img{
    width: 100%;
}
.visit{
    color: #3c3c3c;
    font-size: 22px;
    font-family: 'Prata',Arial,sans-serif;
    line-height: 1.35;
    font-weight: 400;
    position: absolute;
    top: 60%;
    right: 0;
    width: 200px;
}
.sectionTen .col-lg-6{
    position: relative;

}
.sectionTen .new{
    padding: 50px;
    min-height: 300px;
}
.sectionTen .circle{
    margin: 0;
}
.sectionTen .dis{
    color: #3c3c3c;
    font-size: 22px;
    font-family: 'Prata',Arial,sans-serif;
    line-height: 1.35;
    font-weight: 400;
}
.sectionTen .circleImg{
    width: 200px;
    height: 200px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}
.sectionEleven{
    background-color: #194e3c !important;
    position: relative;
    margin-top: 150px;
}
.sectionEleven .second{
    color: white;
    padding: 80px 0px;
}
.sectionEleven .call_btn{
    background: #fff;
    color: #005a8c;
    padding: 5px 10px;
    border-radius: 30px;
}
.sectionEleven .name{
    color: #ffffff;
    font-size: 28px;
    font-family: 'Inter',Arial,sans-serif;
    line-height: 1.35;
    font-weight: 300;
    text-transform: uppercase;
}
.sectionEleven .address{
    font-size: 18px;
}
.sectionEleven .second div, .sectionEleven .second button{
    margin-bottom: 20px;
}
.footerImg{
    position: absolute;
    bottom: 0;
    right: 0;
}
.sectionEleven .logo{
    margin-bottom: 50px;
}